import axios from 'axios'
import { required } from 'vuelidate/lib/validators'
export default {
    name: 'upload-cv',
    data() {
        return {
            docCv: null,
            employeeId: this.$store.getters.getUid,
            errorMessage: ''
        }
    },
    validations: {
        docCv: { required }
    },
    methods: {
        handleSubmit() {
            this.$v.$touch()
            if (this.$v.$invalid) {
                this.error = 'Please complete mandatory fields'
            } else {
                this.$bvModal.show('modal-confirmation')
            }
        },
        handleModalConfirmationOk() {
            this.$bvModal.show('modal-loading')
            let formData = new FormData()
            formData.append('doc_cv', this.docCv)
            setTimeout(() => {
                this.$http({
                    method: 'put',
                    url: `/employee/cv/managed-cv/${this.employeeId}`,
                    headers: {
                        Authorization: 'Bearer ' + this.$store.getters.getToken,
                        'Content-Type': 'multipart/form-data'
                    },
                    data: formData,
                    timeout: 1000
                })
                // axios({
                //     method: 'put',
                //     url: 'https://178.128.98.236:7070/lawfirm-apps/employee/cv/managed-cv/' + this.employeeId,
                //     headers: {
                //         Authorization: 'Bearer ' + this.$store.getters.getToken,
                //         'Content-Type': 'multipart/form-data'
                //     },
                //     data: formData,
                //     timeout: 500
                // })
                .then(response => {
                    console.log('Success', response.data)
                    this.$bvModal.hide('modal-loading')
                })
                .catch(error => {
                    console.log('Error', error.response.data)
                    this.$bvModal.hide('modal-loading')
                })
            }, 500)
        },
        processDocCv(e) {
            this.docCv = e.target.files[0]
        }
    }
}